@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
/*      UTILITY     */

:root {
  --primary-blue: #006994;
  --primary-orange: #942B00;
}

body,
html {
    font-family: 'Roboto', sans-serif;
}

body {
  height: 100%;
  background-color: #006994;
  background-color: var(--primary-blue);
  min-height: 100vh;
  position: relative;
  background-image: linear-gradient(to right, #70a6bb, #006994, #006994, 
  #006994, #006994, #006994, #006994, #006994, 
  #006994, #a5c6d3);
  background-image: linear-gradient(to right, #70a6bb, var(--primary-blue), var(--primary-blue), 
  var(--primary-blue), var(--primary-blue), var(--primary-blue), var(--primary-blue), var(--primary-blue), 
  var(--primary-blue), #a5c6d3);
}


.flex {
  display: flex;
}

a {
  color: black;
  text-decoration: none;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1.15rem;
}

/*      NAVBAR      */
.navbar-container {
  background-color: white;
  height: 100px;
  align-items: center;
  justify-content: center;
  border-bottom: 2px solid #006994;
  border-bottom: 2px solid var(--primary-blue);
}

.navbar-background {
  background-color: #006994;
  background-color: var(--primary-blue);
  width: 100%;
  height: 50%;
}

.navbar {
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  width: 65%;
  height: 100%;
}

.navlogo {
  align-items: center;
  height: 100%;
}

.navlogo h4 {
  margin-left: 10px;
  color: white;
  font-weight: bold;
}

.navlogo img {
  height: 90%;
}

.nav {
  align-items: center;
}

.nav a {
  margin-right: 100px;
  color: white;
}

.nav a:hover {
  text-decoration: underline;
}

.link-searchglass {
  color: white;
  cursor: pointer;
}

.nav-searchbar {
  border-radius: 5px;
  height: 20px;
  padding: 4px 8px;
  width: 180px;
  animation-name: nav-searchbar-anim;
  animation-duration: 1s;
  outline: none;
  border: none;
}

@keyframes nav-searchbar-anim {
  from {width: 10px;}
  to {width: 180px;}
}

/*      THUMBNAIL     */

.thumbnail-container {
  position: relative;
  width: 100%;
  height: 320px;
  justify-content: center;
  align-items: flex-end;
  background-image: linear-gradient(white, #006994);
  background-image: linear-gradient(white, var(--primary-blue));
}

.thumbnail-container::before {
  content: '';
  position: absolute;
  background-image: url(/fishpedia/static/media/thumbnail1.5e0d31aa.jfif);
  background-color: #006994;
  background-color: var(--primary-blue);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  opacity: 0.5;
  width: 100%;
  height: 100%;
}

.thumbnail-container h2 {
  position: relative;
  text-align: center;
  color: white;
  font-weight: lighter;
}

.thumbnail-text {
  max-width: 600px;
  margin-bottom: 50px;
}

/*      SHOWCASE      */

.showcase-container {
  background-color: white;
}

.showcase {
  min-height: 200px;
  width: 100%;
  max-width: 1000px;
  margin: 0 auto;
  justify-content: space-between;
  padding: 50px 0 55px;
}

.showcase-item {
  text-align: center;
  width: 200px;
  color: white;
  background: #006994;
  background: var(--primary-blue);
  padding: 30px 25px 5px;
  border-radius: 15px;
  line-height: 1.5rem;
}

.showcase-item a {
  color: white;
  text-decoration: underline;
}

.showcase-item h5 {
  margin-bottom: 25px;
}

/*      RANDOM CATCH      */

.random-catch-container {
  align-items: center;
  flex-direction: column;
  padding-top: 50px;
  color: white;
  padding-bottom: 180px;
  max-width: 2000px;
  margin: 0 auto;
}

.random-catch-container h4 {
  margin-bottom: 40px;
  font-size: 2rem;
}

.random-catch {
  width: 100%;
}

.random-catch-item-container {
  flex-wrap: wrap;
  width: 100%;
}

.random-catch-item {
  width: 25%;
  text-align: center;
}

.random-catch-item h6 {
  color: white;
  margin-bottom: 10px;
}

.random-catch-item img {
  width: 50%;
  height: calc(20vw * 0.5625);
  margin-bottom: 30px;
  border-radius: 10px;
  outline: 1px solid white;
}

.random-catch-button {
  border: none;
  border-radius: 8px;
  padding: 10px 50px;
  margin: 10px 0 50px;
  cursor: pointer;
  color: #942B00;
  color: var(--primary-orange);
  font-weight: bold;
  outline: 1px solid #942B00;
  outline: 1px solid var(--primary-orange);
}

.random-catch-button:active {
  outline: 1px solid white;
  background-color: #942B00;
  background-color: var(--primary-orange);
  color: white;
}

/*      FOOTER      */

.footer {
  padding-top: 150px;
  background-color: rgba(0, 0, 0, 0.801);
  position: absolute;
  bottom: 0;
  width: 100%;
}

/*      FISH INFO CONTAINER     */

.fish-info-container {
  max-width: 1500px;
  width: 80%;
  margin: 0 auto;
  color: white;
}

.info-name {
  margin: 35px 0 65px;
}

.info-name h2 {
  margin-bottom: 7px;
}

.info-name h6 {
  font-style: italic;
  font-weight: lighter;
  font-size: 0.95rem;
}

.info-item {
  padding-bottom: 25px;
  margin-bottom: 25px;
  border-bottom: 1px solid white;
  min-height: 150px;
}

.info-item ul {
  line-height: 1.5rem;
  list-style-type: circle;
  margin-left: 22px;
}

.info-item a {
  color: white;
  text-decoration: underline;
}

.info-item h3 {
  margin-bottom: 15px;
}

.info-images {
  border-bottom: none;
  padding-bottom: 230px;
  margin-bottom: 0;
}

.info-images-list {
  flex-wrap: wrap;
  margin-left: 10px;
}

.info-images-list a {
  width: 18%;
  margin-right: 20px;
  margin-bottom: 20px;
}

.info-images-list img {
  width: 100%;
  height: calc(20vw * 0.5625);
  outline: 1px solid white;
  border-radius: 8px;
}

.info-float-container {
  float: right;
  width: 23%;
}

.info-float {
  background-color: white;
  width: 100%;
  margin-left: 50px;
  border-radius: 15px;
  padding-bottom: 15px;
}

.info-float-img {
  justify-content: center;
  padding-top: 15px;
}

.info-float-img img {
  width: 90%;
  margin: 0 auto;
  border-radius: 12px 12px 5px 5px;
  outline: 1px solid #006994;
  outline: 1px solid var(--primary-blue);
  max-height: calc(25vw * 0.5625);
}

.info-float-stats-container,
.info-float-taste-container {
  width: 90%;
  margin: 0 auto;
  margin-top: 30px;
  background-color: #006994;
  background-color: var(--primary-blue);
  border-radius: 10px;
  padding: 20px 0 25px;
}

.info-float-stats-container a,
.info-float-taste-container a {
  color: white;
  text-decoration: underline;
}

.info-float-taste-container {
  margin-top: 15px;
}

.info-float-first-stat {
  margin-top: 30px;
}

.info-float-stats {
  padding: 0 10px 30px;
  text-align: center;
}

.info-float-stats-bottom {
  padding: 0 10px;
  text-align: center;
}

.info-float-stat-title {
  font-weight: bold;
  margin-bottom: 5px;
  font-size: 0.9rem;
}

.info-float-stat-body {
  line-height: 1.2rem;
  font-size: 0.85rem;
}

/*      SEARCH      */

.fish-search-container {
  max-width: 1500px;
  width: 80%;
  margin: 0 auto;
  color: white;
  flex-wrap: wrap;
  text-align: center;
  padding-bottom: 230px;
}

.search-item {
  width: 33%;
  height: calc(33vw * 0.5625);
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.search-item-title {
  margin: 30px 0;
  cursor: pointer;
}

.search-item img {
  width: 60%;
  height: 60%;
  outline: 1px solid white;
  border-radius: 8px;
  cursor: pointer;
}

.search-no-results {
  width: 100%;
  padding-top: 100px;
  font-size: 2rem;
}

@media (max-width: 2000px) {
  .random-catch-container {
    background-image: linear-gradient(#006994, #006994, #006994, white);
    background-image: linear-gradient(var(--primary-blue), var(--primary-blue), var(--primary-blue), white);
  }
}

@media (max-width: 1024px) {
  .showcase {
    max-width: 900px;
  }
}

@media (max-width: 768px) {
  .navbar {
    width: 80%;
  }

  .thumbnail-container {
    height: 280px;
  }

  .showcase {
    max-width: 700px;
  }

  .showcase-item {
    text-align: center;
    width: 150px;
    color: white;
  }  

  .info-float-container {
    width: 35%;
  }

  @media (max-width: 450px) {
    .navbar {
      width: 100%;
      justify-content: space-evenly;
    }
    
   .navlogo h4 {
      margin-right: 20px;
    }

    .nav a {
      margin-right: 20px;
    }

    .showcase {
      justify-content: center;
      align-items: center;
      flex-direction: column;
      padding: 30px 0 15px;
  }
  
    .showcase-item {
      height: 150px;
      width: 250px;
      margin-bottom: 20px;
    }

    .random-catch-container {
      padding-top: 35px;
    }

    .random-catch-item {
      width: 50%;
      text-align: center;
    }
    
    .random-catch-item img {
      width: 45%;
      height: calc(25vw * 0.5625);
    }
  }

  @media (max-width: 320px) {
    .showcase-item {
      width: 220px;
    }
  }
  
}
